.todos {
    width: 500px;
    margin: auto;
    max-width: 100%;
}

.input {
    display: flex;
}
input {
    width: 100%;
    padding: 5px;
    font-size: 18px;
    border-radius: 5px;
    border: 1px solid lightgray;
    margin-right: 5px;
}

.button {
    border-radius: 5px;
    background-color: darkviolet;
    color: white;
    border: none;
    padding: 0 20px;
    cursor: pointer;
}

.items {
    margin-top: 15px;
    border-top: 1px solid lightgray;
}

.todo {
    font-size: 18px;
    border-bottom: 1px solid lightgray;
    padding: 15px 0;
    display: flex;
    justify-content: space-between;
}

.del {
    width: 60px;
    font-size: 10px;
    text-align: center;
    background-color: rgb(153, 65, 190);
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}